.sitemap-head-text-style {
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.015em;
  font-feature-settings: "liga" 0;
  line-height: 1.5;
  font-size: 18px;
}

.sitemap-hr {
  color: var(--theme-secondary);
  width: 80%;
  height: 3px !important;
}

.list-un-styled {
  padding-left: 0;
  list-style: none;
}

.list-un-styled li {
  margin-bottom: 5px;
}

.list-un-styled a {
  text-decoration: none;
  color: #00b1ff;
  font-size: 17px;
  transition-duration: 0.2s;
  list-style-type: none;
  letter-spacing: 0.015em;
}
