.call-req-div {
  background-color: #f6f9fa;
}

.call-req-div hr {
  width: 200px;
  margin-top: 4px;
  color: var(--theme-secondary);
  height: 3px !important;
  margin: auto;
}

.call-req-div h2 {
  margin-bottom: 0px;
  font-weight: 700;
  letter-spacing: 0.015em;
}

.call-req-div p {
  font-size: 20px;
}

.call-req-div a {
  font-size: 20px;
  font-weight: bold;
}

.hr-tag-blue {
  background: var(--theme-link);
  content: "";
  display: block;
  height: 4px !important;
  margin: 10px 0 30px 0;
  width: 100px;
  opacity: 1;
}
